@keyframes translation{
    0% {
        transform:scale(1)  translateX(-5%); 
      }
      100% {
        transform:scale(1.0)  translateX(0%);
      }
    }

   .get-in-touch-title{
    animation: translation  0.6s ease;
   } 
.contact_container{
    grid-template-columns: 4fr 8fr;
}
.contact_title{
    font-size: var(--h3-font-size);
    font-weight: var(--weight-600);
    margin-bottom: 1rem;
}

.contact_description{
    font-size: var(--small-font-size);
    color: var(--title-color);
    line-height: 1.6;
}
.contact_description,.contact_info .info_item{
    margin-bottom: 2rem;
}
.contact_info .info_title{
    /* background-color: blue; */
    color: var(--title-color);
    font-size: var(--small-font-size);


}
.contact_info .info_item{
    display: flex;
    align-items: center;
    
    column-gap: 1rem;
}

.contact_info .info_icon{
    color: var(--first-color);
    font-size: var(--h2-font-size);
}

.contact_info .info_title{
    font-size: var(--small-font-size);
}

.contact_info .info_desc{
    font-family: var(--body-font);
    font-size: var(--small-font-size);
    font-weight: var(--weight-600);
    opacity: 0.8;
}
.contact_socials{
    display: flex;
    column-gap: 1rem;

}
.contact_social_links{
    background-color: var(--container-color);
    color: var(--title-color);
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    text-align: center;
    line-height: 3.2rem;
    transition: all 0.3s ease;
}
.contact_social_links:hover{
    background-color: var(--first-color);
    color: var(--text-white);
}
.contact_form .form_input-group .form_input-div{
    display: flex;
   
   flex-wrap: nowrap;
   justify-content: space-evenly;
    
}
.contact_form .form_input-group .form_input-div input{
    width: 100%;
    margin: 1rem;
}
.form_input-div{
    margin-bottom: 2rem;
    
}
.form_control{
    border:0.1rem solid var(--container-color);
    background-color: rgba(236, 236, 236, 0.579);
    
    color: var(--title-color);
    width: 100%;
    padding: 1rem 1.5rem;
    border-radius: 30px;
    font-size: var(--small-font-size);
    transition: all 0.3s ease;
}
.form_control:focus{
    border-color: var(--first-color);

}
.textarea{
    resize: none;
    height: 11rem;
}

.contact_button-icon{
    font-size: var(--h3-font-size);
    line-height:4rem;
}

/* Responsiveness*/

@media screen and (max-width:425px) {
    :root{
        font-size:45%;
    }
 .form_input-div{
        width: 90vw;
        margin:1.5rem auto;
    }
    .contact_container{
      grid-template-columns: 1fr; 

padding:0 2rem ;
      row-gap: 4rem;  
    }
    .send-msg{
        width: 160px;
        margin:1rem 25vw;
        /* padding:2.2rem; */

    }
}

@media (min-width:425px) and (max-width:767px) {
    :root{
        font-size:60%;
    }
 .form_input-div{
        width: 85vw;
        margin:1.5rem auto;
        
    }
    .contact_container{
      grid-template-columns: 1fr; 

padding:0rem 2rem ;
      row-gap: 2rem;  
    }
    .send-msg{
       
        margin:1rem 25vw;
        /* padding:2.2rem; */

    }
}

@media (min-width:768px) and (max-width:1023px) {
    :root{
        font-size:80%;
    }
 .form_input-div{
        /* width: 85vw; */
        /* margin:1.5rem auto; */
        
    }
    .contact_container{
      /* grid-template-columns: 1fr 1fr;  */
        column-gap: 5rem;
        padding: 2rem;
    }
    .send-msg{
       
        /* margin:1rem 25vw; */
        /* padding:2.2rem; */

    }
}

@media (min-width:1024px) and (max-width:1439px) {
    :root{
        font-size:90%;
    }

    .contact_container{
      grid-template-columns: 2fr 5fr;
        column-gap: 1rem;
        padding: 2rem;
    }
    .form_input-div{
        width: 55vw;
    }
    
}
@media screen and (min-width:2560px) {
    :root{
        font-size:150%;
    }

    .contact_container{
      grid-template-columns: 1fr 1fr;
        column-gap: 5rem;
        padding: 2rem;
    }
    .form_input-div{
        width: 45vw;
    }
    .send-msg{
        width: 300px;
        height: 3rem;
        border-radius: 2rem;
    }
    .send-msg .contact_button-icon{
        font-size:1rem ;
        width: 3rem;
        /* height: 2rem; */
        display: flex;
        justify-content: center;
        align-items: center;
    }
}